<template>
    <div>
        <v-card flat>
            <v-subheader>Information Nutrilon {{ food.nama_makanan }}</v-subheader>
            <v-row align="center" justify="center">
                <img v-if="food.slug" :src="`${baseUrl}/api/foods/images/${food.slug}.jpg`" alt="Food Image" height="200px"
                    class="rounded-xl outlined" color="purple" />
            </v-row>
            <v-container>
                <v-simple-table>
                    <tbody>
                        <tr v-if="food.nama_makanan">
                            <th>Nama Makanan</th>
                            <td>{{ food.nama_makanan }}</td>
                        </tr>
                        <tr v-if="food.kalori_kcal !== null">
                            <th>Kalori (kcal)</th>
                            <td>{{ food.kalori_kcal }}</td>
                        </tr>
                        <tr v-if="food.kolestrol_mg !== null">
                            <th>Kolestrol (mg)</th>
                            <td>{{ food.kolestrol_mg }}</td>
                        </tr>
                        <tr v-if="food.lemak_g !== null">
                            <th>Lemak (g)</th>
                            <td>{{ food.lemak_g }}</td>
                        </tr>
                        <tr v-if="food.karbohidrat_g !== null">
                            <th>Karbohidrat (g)</th>
                            <td>{{ food.karbohidrat_g }}</td>
                        </tr>
                        <tr v-if="food.protein_g !== null">
                            <th>Protein (g)</th>
                            <td>{{ food.protein_g }}</td>
                        </tr>
                        <tr v-if="food.kalium_mg !== null">
                            <th>Kalium (mg)</th>
                            <td>{{ food.kalium_mg }}</td>
                        </tr>
                        <tr v-if="food.nutrisi !== null">
                            <th>Nutrisi</th>
                            <td>{{ food.nutrisi }}</td>
                        </tr>
                        <tr v-if="food.ingredient !== null">
                            <th>Ingredient</th>
                            <td>{{ food.ingredient }}</td>
                        </tr>
                    </tbody>
                </v-simple-table>
            </v-container>
        </v-card>
    </div>
</template>
<script>
export default {
    data() {
        return {
            baseUrl: process.env.VUE_APP_BACKEND_URL,
            food: {},
        };
    },
    created() {
        let slug = this.$route.params.slug;
        this.axios
            .get(`/api/foods`, {
                params: {
                    f: slug,
                },
            })
            .then((response) => {
                if (response.data && response.data.length > 0) {
                    // Assuming you want the first item from the array
                    this.food = response.data[0];
                    // console.log(this.food);
                } else {
                    console.error('No data found');
                }
            })
            .catch((error) => {
                let response = error.response;
                console.log(response);
            });
    },
};
</script>